function adicionarVitoria(index) {
	listPlayer[index].vitoria++;
	listPlayer[index].pontos = listPlayer[index].pontos + 3;
	exibirNaTela();
}

function adicionarEmpate(index) {
	listPlayer[index].empate++;
	exibirNaTela();
}

function adicionarDerrota(index) {
	listPlayer[index].derrota++;
	exibirNaTela();
}

function removerVitoria(index) {
	if (listPlayer[index].vitoria > 0) {
		listPlayer[index].vitoria--;
		listPlayer[index].pontos = listPlayer[index].pontos - 3;
		exibirNaTela();
	} else {
		alert("O jogador não possui vitórias e serem removidas.");
	}
}

function removerEmpate(index) {
	if (listPlayer[index].empate > 0) {
		listPlayer[index].empate--;
		exibirNaTela();
	} else {
		alert("O jogador não possui empates a serem removidos.");
	}
}

function removerDerrota(index) {
	if (listPlayer[index].derrota > 0) {
		listPlayer[index].derrota--;
		// Supondo que remover uma derrota não altera os pontos (pode ser diferente dependendo da lógica do seu aplicativo)
		exibirNaTela();
	} else {
		alert("O jogador não possui derrotas a serem removidas.");
	}
}

function apagarLista() {
	listPlayer = [];
	exibirNaTela();
}

function exibirNaTela() {
	elementoTabela.innerHTML = "";

	listPlayer.forEach((jogador, index) => {
		console.log(jogador);

		elementoTabela.innerHTML += `
    <tr>
        <td>${jogador.name}</td>
        <td>${jogador.vitoria}</td>
        <td>${jogador.empate}</td>
        <td>${jogador.derrota}</td>
        <td>${jogador.partidas}</td>
        <td>${jogador.pontos}</td>
        
        <td>
        <button class="winner" onClick="adicionarVitoria(${index})">Vitória</button>
        <button class="remove" onClick="removerVitoria(${index})">Remover</button>
        </td>

        <td>
        <button class="aTie" onClick="adicionarEmpate(${index})">Empate</button>
        <button class="remove" onClick="removerEmpate(${index})">Remover</button>
        </td>
        
        <td>
        <button class="defeat" onClick="adicionarDerrota(${index})">Derrota</button>
        <button class="remove" onClick="removerDerrota(${index})">Remover</button>
        </td>
    </tr>
`;
	});
}

function addPlayer() {
	var namePlayer = document.getElementById("name").value;

	if (namePlayer.trim() === "") {
		alert("Por favor, preencha o nome do Jogador.");
		return;
	}

	listPlayer.push({
		name: namePlayer,
		vitoria: 0,
		empate: 0,
		derrota: 0,
		pontos: 0
	});
	document.getElementById("name").value = "";
	exibirNaTela();
}

var listPlayer = [];

var elementoTabela = document.getElementById("tabelaJogadores");

exibirNaTela();
